        .get('/crops', {
          params: {
            cntnts: val
          }})
        .then(response => {
          this.cropName = response.data.basicInfo.cntntsSj._cdata;
          this.basicInfo = response.data.basicInfo.cn._cdata;
          this.scheduleInfo = response.data.schedule._cdata;
          this.updateInfo();
        })
        .catch(error => {
          console.log(error)
        })
    }
  }
